@import url(https://fonts.googleapis.com/css?family=Galindo|Lato|Open+Sans|Skranji&display=swap);
.Preloader {
    align-items: center;
    background: #000000;
    background: radial-gradient(circle, rgba(34,68,244,1) 0%, rgba(249,26,26,1) 80%);
    display: flex;
    flex-direction: column;
    font-family: 'Open Sans', sans-serif;
    height: 100vh;
    justify-content: center;
    position: absolute;
    width: 100vw;
}

.Preloader .Text {
    margin-top: 32px;
}
html, body {
    margin: 0;
    overflow-x: hidden;
    padding: 0;
}

#SiteRoot {
    background: #000000;
    color: white;
}

.sectionTitle {
    color: white;
    font-family: 'Galindo', cursive;
    font-weight: bold;
}
